/*
	SOCIAL
*/

.social {
	text-align: center;
	margin-bottom: 34px;

	&__item {
		display: inline-block;
		vertical-align: middle;
		margin: 0 10px;
	}

	&__link {
		display: block;
		text-decoration: none;

		& svg {
			fill: #0a44aa;
			transition: fill $transition;
		}

		&:hover svg {
			fill: #004cd1;
		}

	}

	&__title {
		display: none;
	}

}